'use client';

import { 
    Pencil, Trash2, Loader2, Search, ChevronLeft, ChevronRight, 
    AlertCircle, Clock, CheckCircle2, Ban, CheckSquare 
} from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge'; 
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import { StatusBooking } from '@/types';

interface StatusBookingTableProps {
    loading: boolean;
    statuses: StatusBooking[];
    totalData: number;
    currentPage: number;
    itemsPerPage: number;
    onPageChange: (page: number) => void;
    onEdit: (item: StatusBooking) => void;
    onDelete: (id: number) => void;
}

export default function StatusBookingTable({
    loading,
    statuses,
    totalData,
    currentPage,
    itemsPerPage,
    onPageChange,
    onEdit,
    onDelete
}: StatusBookingTableProps) {
    
    const totalPages = Math.ceil(totalData / itemsPerPage);
    const startNumber = (currentPage - 1) * itemsPerPage + 1;

    // Helper untuk Warna & Ikon Badge
    const getStatusBadge = (name: string) => {
        const n = name.toLowerCase();
        
        if (n.includes('pembayaran') || n.includes('pending')) {
            return (
                <Badge variant="outline" className="bg-yellow-50 text-yellow-700 border-yellow-200 hover:bg-yellow-100">
                    <AlertCircle className="w-3 h-3 mr-1" /> Menunggu Pembayaran
                </Badge>
            );
        }
        if (n.includes('konfirmasi')) {
            return (
                <Badge variant="outline" className="bg-blue-50 text-blue-700 border-blue-200 hover:bg-blue-100">
                    <Clock className="w-3 h-3 mr-1" /> Menunggu Konfirmasi
                </Badge>
            );
        }
        if (n.includes('terkonfirmasi') || n.includes('confirmed')) {
            return (
                <Badge variant="outline" className="bg-green-50 text-green-700 border-green-200 hover:bg-green-100">
                    <CheckCircle2 className="w-3 h-3 mr-1" /> Terkonfirmasi
                </Badge>
            );
        }
        if (n.includes('batal') || n.includes('cancel')) {
            return (
                <Badge variant="outline" className="bg-red-50 text-red-700 border-red-200 hover:bg-red-100">
                    <Ban className="w-3 h-3 mr-1" /> Dibatalkan
                </Badge>
            );
        }
        if (n.includes('selesai') || n.includes('done')) {
            return (
                <Badge variant="outline" className="bg-gray-100 text-gray-700 border-gray-300 hover:bg-gray-200">
                    <CheckSquare className="w-3 h-3 mr-1" /> Selesai
                </Badge>
            );
        }

        // Default Badge
        return <Badge variant="secondary">{name}</Badge>;
    };

    return (
        <div className="rounded-md border bg-white shadow-sm overflow-hidden">
            <Table>
                <TableHeader>
                    <TableRow className="bg-gray-50">
                        <TableHead className="w-[60px] text-center font-semibold text-gray-600">No</TableHead>
                        <TableHead className="font-semibold text-gray-600">Nama Status</TableHead>
                        <TableHead className="font-semibold text-gray-600">Keterangan / Badge</TableHead>
                        <TableHead className="w-[100px] font-semibold text-gray-600 text-center">ID</TableHead>
                        <TableHead className="text-right font-semibold text-gray-600">Aksi</TableHead>
                    </TableRow>
                </TableHeader>
                <TableBody>
                    {loading ? (
                        <TableRow>
                            <TableCell colSpan={5} className="h-24 text-center text-gray-500">
                                <Loader2 className="h-5 w-5 animate-spin inline mr-2" /> Memuat data...
                            </TableCell>
                        </TableRow>
                    ) : statuses.length === 0 ? (
                        <TableRow>
                            <TableCell colSpan={5} className="h-40 text-center text-gray-500">
                                <div className="flex flex-col items-center justify-center">
                                    <Search className="h-10 w-10 text-gray-300 mb-3" />
                                    <p className="font-medium">Data status tidak ditemukan.</p>
                                    <p className="text-xs mt-1">Coba tambahkan status baru.</p>
                                </div>
                            </TableCell>
                        </TableRow>
                    ) : (
                        statuses.map((item, index) => (
                            <TableRow key={item.id} className="hover:bg-gray-50 transition-colors">
                                <TableCell className="text-center text-gray-500 font-medium">
                                    {startNumber + index}
                                </TableCell>
                                <TableCell className="font-medium text-gray-900">
                                    {item.nama_status}
                                </TableCell>
                                <TableCell>
                                    {getStatusBadge(item.nama_status)}
                                </TableCell>
                                <TableCell className="text-center text-xs text-gray-400 font-mono bg-gray-50 rounded mx-auto w-fit px-2 py-1">
                                    ID: {item.id}
                                </TableCell>
                                <TableCell className="text-right space-x-2">
                                    <Button 
                                        variant="ghost" 
                                        size="icon" 
                                        onClick={() => onEdit(item)}
                                        className="hover:bg-blue-50 hover:text-blue-600 transition-colors"
                                    >
                                        <Pencil className="h-4 w-4" />
                                    </Button>
                                    {/* Proteksi ID 1-5 (Status Bawaan Sistem) agar tidak dihapus sembarangan */}
                                    <Button 
                                        variant="ghost" 
                                        size="icon" 
                                        onClick={() => onDelete(item.id)}
                                        disabled={item.id <= 5} 
                                        title={item.id <= 5 ? "Status bawaan sistem tidak bisa dihapus" : "Hapus Status"}
                                        className={`hover:bg-red-50 hover:text-red-600 transition-colors ${item.id <= 5 ? "opacity-30 cursor-not-allowed" : ""}`}
                                    >
                                        <Trash2 className="h-4 w-4" />
                                    </Button>
                                </TableCell>
                            </TableRow>
                        ))
                    )}
                </TableBody>
            </Table>

            {/* Pagination */}
            {!loading && totalData > 0 && (
                <div className="flex items-center justify-between p-4 border-t bg-gray-50/50">
                    <div className="text-sm text-gray-500">
                         Menampilkan <strong>{statuses.length}</strong> dari <strong>{totalData}</strong> status
                    </div>
                    <div className="flex items-center space-x-2">
                        <Button 
                            variant="outline" 
                            size="sm" 
                            onClick={() => onPageChange(currentPage - 1)} 
                            disabled={currentPage === 1}
                            className="h-8 w-8 p-0"
                        >
                            <ChevronLeft className="h-4 w-4" />
                        </Button>
                        <span className="text-sm font-medium px-2">Halaman {currentPage}</span>
                        <Button 
                            variant="outline" 
                            size="sm" 
                            onClick={() => onPageChange(currentPage + 1)} 
                            disabled={currentPage === totalPages}
                            className="h-8 w-8 p-0"
                        >
                            <ChevronRight className="h-4 w-4" />
                        </Button>
                    </div>
                </div>
            )}
        </div>
    );
}